.sidebar {
  width: 50px;
  background: #7386d5;
  color: #fff;
  transition: all 0.5s;

  &.is-open {
    width: 400px;
  }

  .toggleButton {
    display: block;
    margin: 0.5rem;
    margin-left: auto;
  }

  .flex-column.nav-pills .nav-item {
    padding-right: 0;
    cursor: pointer;
  }
}

@media only screen and (max-width: 580px) {
  .sidebar {
    position: absolute;
    height: 100%;
    z-index: 100;
  }

  .sidebar-content {
    margin-left: 50px;
  }
}
